import React, { useEffect, useRef, useState, useCallback } from "react";
import styles from './index.module.scss';
import { useSpring, animated, useTransition } from '@react-spring/web';
import {
  manualClick,
  selectManualClickPower,
  selectResources,
  selectSpheres,
  selectStage,
  selectedCountry,
  setStage,
  selectShowMinistryPopup,
  hideMinistryPopup,
  selectTotal,
} from "../../app/gameSlice";
import { showCoinEffectThunk } from "../../app/visualEffectsSlice";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { Shop } from "./shop";
import { Feature } from "geojson";
import { CountryMap } from "../map/CountryMap";
import { GOLD_COLORS } from "../../constants";
import TalentTree from "../../components/TalentTree";
import Projects from '../../components/Projects';
import ResourcesHeader from "../../components/ResourcesHeader";
import { Cosmos } from "./cosmos";
import { InstructionPopup } from "../../components/InstructionPopup";
import ShieldControls from "../../components/ShieldControls";
import { QuestSystem } from "../../components/QuestSystem/QuestSystem";
import { MinistryPopup } from "../../components/MinistryPopup/MinistryPopup";
import classNames from "classnames";

//  
const Tooltip = ({ text }: { text: string }) => {
  return (
    <div className={styles.tooltip}>
      {text}
    </div>
  );
};

interface Particle {
  x: number;
  y: number;
  vx: number;
  vy: number;
  life: number;
  color: string;
  size: number;
}

interface InterfaceProps {
  geo: Feature | null;
  clickPosition: { x: number; y: number } | null;
  isVisible?: boolean;
}


export const Step1 = ({ geo, clickPosition, isVisible = false }: InterfaceProps) => {
  const dispatch = useAppDispatch();
  const selectedCountryName = useAppSelector(selectedCountry);
  const stage = useAppSelector(selectStage);
  const total = useAppSelector(selectTotal);
  const showMinistryPopup = useAppSelector(selectShowMinistryPopup);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const mapContainerRef = useRef<HTMLDivElement>(null);
  const manualClickPower = useAppSelector(selectManualClickPower);
  const particles = useRef<Particle[]>([]);
  const [isClicking, setIsClicking] = useState(false);
  const [finishedLoading, setFinishedLoading] = useState(false);
  const [showInstructions, setShowInstructions] = useState(false);
  const [isButtonJumping, setIsButtonJumping] = useState(false);
  const [headerWireframe, setHeaderWireframe] = useState(true);
  const [projectsWireframe, setProjectsWireframe] = useState(true);
  const [upgradesWireframe, setUpgradesWireframe] = useState(true);
  const [developmentWireframe, setDevelopmentWireframe] = useState(true);

  //       

  //   
  const handleCloseInstructions = () => {
    setShowInstructions(false);
  };

  //  
  const initParticles = (x: number, y: number) => {
    //   
    particles.current = [];
    
    //      canvas
    if (canvasRef.current) {
      const rect = canvasRef.current.getBoundingClientRect();
      const canvasX = x - rect.left;
      const canvasY = y - rect.top;
      
      //   
      for (let i = 0; i < 30; i++) {
        const angle = Math.random() * Math.PI * 2; //    (360 )
        const speed = 0.1 + Math.random() * 1; //  
        
      particles.current.push({
          x: canvasX,
          y: canvasY,
          vx: Math.cos(angle) * speed, //     X
          vy: Math.sin(angle) * speed, //     Y
          life: 2, //   
          color: GOLD_COLORS[Math.floor(Math.random() * GOLD_COLORS.length)],
          size: 1 + Math.random() * 2 //     
        });
      }

    }
  };

  //  
  const drawParticles = useCallback(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    //   canvas
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    //   ,  
    if (particles.current.length === 0) {
      requestAnimationFrame(drawParticles);
      return;
    }

    particles.current = particles.current.filter(p => {
      //  
      p.x += p.vx;
      p.y += p.vy;
      
      //   
      p.vx *= 0.98;
      p.vy *= 0.98;
      
      //    ()
      p.life -= 0.01;

      //  ,    
      if (p.life > 0) {
        ctx.save();
        ctx.fillStyle = p.color;
        ctx.globalAlpha = p.life; //     
        
        //  
      ctx.beginPath();
        ctx.arc(p.x, p.y, p.size, 0, Math.PI * 2);
      ctx.fill();

        //  
        ctx.restore();
      }

      //  ,   
      return p.life > 0;
    });

    requestAnimationFrame(drawParticles);
  }, []);

  //     
  const handleCountryClick = (pos: { x: number; y: number }) => {
    setIsClicking(true);
    setTimeout(() => setIsClicking(false), 100);
    
    dispatch(manualClick());

    //       
    dispatch(showCoinEffectThunk({
      position: pos,
      value: manualClickPower,
      prefix: '+',
      suffix: ''
    }));
    
    //  ,    
    initParticles(pos.x, pos.y);
  };

  //   
  useEffect(() => {
    const animationId = requestAnimationFrame(drawParticles);
    return () => cancelAnimationFrame(animationId);
  }, [drawParticles]);


  //   canvas      
  useEffect(() => {
    const updateCanvasSize = () => {
      const canvas = canvasRef.current;
      const container = mapContainerRef.current;
      if (canvas && container) {
        //   
        const { width, height } = container.getBoundingClientRect();
        
        //   canvas   
        canvas.width = width;
        canvas.height = height;
        
        console.log("Canvas resized to:", width, height);
      }
    };

    //    
    updateCanvasSize();
    
    //     
    window.addEventListener('resize', updateCanvasSize);
    
    return () => window.removeEventListener('resize', updateCanvasSize);
  }, []);

  useEffect(() => {
    setTimeout(() => {
      setFinishedLoading(true);
    }, 1000);
  }, []);

  useEffect(() => {
    if (total > 1) {
      setHeaderWireframe(false);
    }
    if (total > 10){
      setUpgradesWireframe(false);
    }
    if (total > 100){
      setDevelopmentWireframe(false);
    }
    if (total > 1000){
      setProjectsWireframe(false);
    }
  }, [total]);


  return (
    <div className={`step1 ${styles.step1} ${isVisible ? styles.visible : ''}`}>
    {showInstructions && (
      <InstructionPopup 
        onClose={handleCloseInstructions} 
        isButtonJumping={isButtonJumping}
        setIsButtonJumping={setIsButtonJumping}
      />
    )}
    <header className={styles.mainHeader}>
      <ResourcesHeader wireframe={headerWireframe} />      
    </header>
    <div className={classNames(styles.development, developmentWireframe && styles.wireframe)}>
      <header className={styles.header}></header>
      <TalentTree />
      {stage === 2 && (
        <div className={styles.shieldControlsContainer}>
          <ShieldControls />
        </div>
      )}
    </div>
    

      <div className={styles.mapContainer} ref={mapContainerRef}>
      {selectedCountryName && (
        <>
        {stage === 2 ? (
          <Cosmos onEarthClick={handleCountryClick} />
        ) : (
          <CountryMap
            onCountryClick={handleCountryClick}
            showOnlySelected={false}
            scale={1}
            className={isClicking ? styles.clicking : ''}
            visible={finishedLoading}
            allowConquest={true}
          />
        )}
          <canvas
            ref={canvasRef}
            className={styles.particlesCanvas}
            style={{ zIndex: 10 }}
          />
        </>
      )}
    </div>

    <div className={classNames(styles.projects, projectsWireframe && styles.wireframe)}>
      <header className={styles.header}></header>
      <Projects />
    </div>
    <div className={classNames(styles.upgrades, upgradesWireframe && styles.wireframe)}>
      <header className={styles.header}>
        
        <div className={styles.tooltipContainer}>
          <span className={styles.infoIcon}></span>
          <Tooltip text="  .    ,        .       ,       ." />
        </div>
      </header>
      <Shop/>
    </div>

    {/*   */}
    <QuestSystem />

    {/*    */}
    {showMinistryPopup && <MinistryPopup />}
    </div>
  );
};
