import { COMMON, ECONOMY, MANUAL } from "../../../app/constants";
import { UPGRADES } from "../../../Upgrades";
import { TALENTS } from "../../../Talents";
import { GameState, GameStrategy } from "./types";
import { 
  calculateConquestCountries, 
  calculateResourcesForConquest, 
  findBestUpgrade, 
  getTotalCPS, 
  initialGameState, 
  purchaseUpgrade, 
  updateGameState 
} from "./formulas";
import { COUNTRY_NAMES } from "../../../constants";

// ,     
const CONQUEST_TALENTS = [
  'global-trader',      // +10%  
  'infrastructure-magnate', // +20%  
  'cooperation-master', // +10%    
  'weapon-domination',  // +20%  , ,   +50%  
  'Brutal-force',       // +50%  , -50%  ,  5-50 
  'triumvirate',        // +10  
];

// ,       
const CONQUEST_UPGRADES = [
  'cursor',    //  
  'hand',      // -
  'factory',   // -
  'drone',     // -
  'market',    // 
  'lab',       // 
  'missionary' // 
];

//     
const TOTAL_COUNTRIES = COUNTRY_NAMES.length; 

//   
export const conquestStrategy: GameStrategy = {
  id: 'conquest',
  name: '  ',
  description: '        ',
  path: ECONOMY,

  //      
  simulateGame: (ticks: number): GameState => {
    //  
    let state = initialGameState();
    const tickLength = 1; //    
    
    //    
    state.resources = 10; //        
    state.manualClickPower = 1;
    
    //    cursor  hand
    if (state.resources >= 10) {
      state = purchaseUpgrade(state, 'cursor');
    }

    //   
    for (let i = 0; i < ticks; i++) {
      //    
      const bestUpgrade = findBestUpgrade(state, CONQUEST_UPGRADES);
      if (bestUpgrade && state.resources >= bestUpgrade.cost) {
        state = purchaseUpgrade(state, bestUpgrade.id);
      }
      
      //    ,   
      for (const talentId of CONQUEST_TALENTS) {
        const talent = TALENTS.find(t => t.id === talentId);
        if (!talent) continue;
        
        //     
        const alreadyActivated = state.talents.some(t => t.id === talentId);
        if (alreadyActivated) continue;
        
        let canActivate = false;
        
        //   
        if (talent.category === ECONOMY && state.spheres[ECONOMY].resources >= talent.position[1] * 1000) {
          canActivate = true;
        }
        
        if (canActivate) {
          //  
          state.talents.push({
            id: talentId,
            name: talent.name,
            sphere: talent.category,
            requiredResources: talent.position[1] * 1000,
            activationTime: state.total,
            effects: {
              conquestSpeed: talent.id === 'global-trader' ? 0.1 : 
                           talent.id === 'weapon-domination' ? 0.5 : 
                           talent.id === 'Brutal-force' ? 0.5 : 
                           talent.id === 'triumvirate' ? 10 : 0,
              multipliers: {
                [ECONOMY]: talent.id === 'weapon-domination' ? 0.2 : 0,
                [COMMON]: 0
              }
            }
          });
          
          //   
          if (talent.id === 'global-trader') {
            state.conquestSpeed += 0.1;
          } else if (talent.id === 'weapon-domination') {
            state.conquestSpeed += 0.5;
            state.multipliers[ECONOMY] += 0.2;
          } else if (talent.id === 'Brutal-force') {
            state.conquestSpeed += 0.5;
            
            //     ( 5  50)
            const randomCountries = Math.floor(Math.random() * 45) + 5;
            state.conqueredCountries += randomCountries;
          } else if (talent.id === 'triumvirate') {
            state.conquestSpeed += 10;
          }
        }
      }
      
      //   ,   
      const resourcesForConquest = calculateResourcesForConquest(state.conqueredCountries + 1);
      if (state.resources >= resourcesForConquest) {
        //   ,  
        const countriesCanConquer = calculateConquestCountries(state.resources, state.conquestSpeed) - state.conqueredCountries;
        if (countriesCanConquer > 0) {
          //    
          let cost = 0;
          for (let c = 0; c < countriesCanConquer; c++) {
            cost += Math.round(500 * Math.pow(1.1, state.conqueredCountries + c));
          }
          state.resources -= cost;
          state.conqueredCountries += countriesCanConquer;
        }
      }
      
      //      
      state = updateGameState(state, tickLength);
      
      // ,     (  )
      if (state.conqueredCountries >= TOTAL_COUNTRIES) {
        break;
      }
    }
    
    return state;
  },

  //     
  estimateCompletion: () => {
    //     ,    
    const maxTicks = 1000000; //   ,   
    const finalState = conquestStrategy.simulateGame(maxTicks);
    
    return {
      goal: `  ${TOTAL_COUNTRIES} `,
      estimatedTime: finalState.total,
      strategy: conquestStrategy.name
    };
  }
}; 