import React, { useMemo } from 'react';
import styles from './Ship.module.scss';
import { useAppDispatch, useAppSelector } from '../../../app/hooks';
import { selectGame } from '../../../app/gameSlice';
import { getAvailableGames, isGameAvailable, getRemainingCooldown, ExtendedMiniGame } from '../../../MiniGames';

//   -
export interface MiniGame {
  id: string;
  name: string;
  description: string;
  cooldown: number; // 
  resourceEffect: {
    type: 'global' | 'economy' | 'science' | 'faith' | 'shield'; //  ,   
    successMultiplier: number; //    (, 1.5 = +50%)
    failureMultiplier: number; //    (, 0.7 = -30%)
    duration: number; //    
  };
  lastUsed?: number; //    (timestamp)
}

//  
export enum ShipSection {
  BRIDGE = 'bridge',
  ENGINE = 'engine',
  RESEARCH = 'research',
  DEFENSE = 'defense'
}

//    
interface SectionProps {
  game: ExtendedMiniGame;
  onGameStart: (game: ExtendedMiniGame) => void;
  isAvailable: boolean;
  cooldown: number | null;
}

//   
const ShipSectionComponent: React.FC<SectionProps> = ({ 
  game,
  onGameStart,
  isAvailable,
  cooldown
}) => {
  const getSectionClass = () => {
    //       
    switch (game.resourceEffect.type) {
      case 'shield':
      case 'economy':
        return styles.engine;
      case 'science':
        return styles.research;
      case 'faith':
        return styles.defense;
      case 'global':
        return styles.bridge;
      default:
        return styles.engine;
    }
  };

  const handleClick = () => {
    if (isAvailable && !cooldown) {
      onGameStart(game);
    }
  };

  return (
    <div 
      className={`
        ${styles.shipSection} 
        ${getSectionClass()} 
        ${isAvailable && !cooldown ? styles.available : ''} 
        ${cooldown ? styles.cooldown : ''} 
        ${!isAvailable ? styles.locked : ''}
      `}
      style={{ 
        left: `${game.sectionPosition.x}%`, 
        top: `${game.sectionPosition.y}%` 
      }}
      onClick={handleClick}
      title={`${game.name} - ${game.description}`}
    >
      {cooldown && (
        <div className={styles.cooldownTimer}>{Math.ceil(cooldown)}s</div>
      )}
      {isAvailable && !cooldown && (
        <div className={styles.readyIndicator}>!</div>
      )}
    </div>
  );
};

interface ShipProps {
  progress: number; //  0  100
  className?: string;
  onGameStart?: (game: ExtendedMiniGame) => void;
}

const Ship: React.FC<ShipProps> = ({ progress, className, onGameStart }) => {
  const game = useAppSelector(selectGame);
  
  //    
  const availableGames = useMemo(() => {
    return getAvailableGames(game);
  }, [game.secondStage, game.stage]);
  
  //      
  const gameStates = useMemo(() => {
    return availableGames.map(miniGame => ({
      game: miniGame,
      isAvailable: isGameAvailable(miniGame, game),
      cooldown: getRemainingCooldown(miniGame, game)
    }));
  }, [availableGames, game.miniGamesState, game]);
  
  //    0  100
  const normalizedProgress = useMemo(() => {
    return Math.max(0, Math.min(100, progress));
  }, [progress]);
  
  //     
  const position = useMemo(() => {
    //    
    const startX = 5; // %   
    const startY = 90; // %   
    const endX = 95; // %   
    const endY = 5; // %   
    
    //       ( )
    const controlX = 25; // %   
    const controlY = 20; // %   
    
    //  t    (0 <= t <= 1)
    const t = normalizedProgress / 100;
    
    //   : B(t) = (1-t)P + 2(1-t)tP + tP
    //  P -  , P -  , P -  
    const x = Math.pow(1 - t, 2) * startX + 2 * (1 - t) * t * controlX + Math.pow(t, 2) * endX;
    const y = Math.pow(1 - t, 2) * startY + 2 * (1 - t) * t * controlY + Math.pow(t, 2) * endY;
    
    return { x, y };
  }, [normalizedProgress]);
  
  //    
  const rotation = useMemo(() => {
    //   45    0    
    return 45 - (normalizedProgress / 100) * 65;
  }, [normalizedProgress]);
  
  //   
  const handleGameStart = (selectedGame: ExtendedMiniGame) => {
    if (onGameStart) {
      onGameStart(selectedGame);
    }
  };

  return (
    <div 
      className={`${styles.shipContainer} ${className || ''}`}
      style={{ 
        left: `${position.x}%`, 
        top: `${position.y}%`,
        transform: `translate(-50%, -50%) rotate(${rotation}deg)`
      }}
    >
      <div className={styles.ship}></div>
      
      {/*       */}
      {gameStates.map(({ game: miniGame, isAvailable, cooldown }) => (
        <ShipSectionComponent
          key={miniGame.id}
          game={miniGame}
          onGameStart={handleGameStart}
          isAvailable={isAvailable}
          cooldown={cooldown}
        />
      ))}
    </div>
  );
};

export default Ship; 