import React, { useState, useEffect, useCallback } from 'react';
import styles from './AlienCodeBreaker.module.scss';
import classNames from 'classnames';

interface AlienCodeBreakerProps {
  onComplete: (success: boolean) => void;
}

//    
const ALIEN_SYMBOLS = ['', '', '', '', '', '', '', '', '', ''];

//  
type LedState = 'off' | 'green' | 'yellow' | 'red';

interface CodeGuess {
  symbols: string[];
  feedback: LedState[];
}

//    
const generateSecretCode = (): string[] => {
  const code: string[] = [];
  const availableSymbols = [...ALIEN_SYMBOLS];
  
  for (let i = 0; i < 4; i++) {
    const randomIndex = Math.floor(Math.random() * availableSymbols.length);
    code.push(availableSymbols[randomIndex]);
    //      
    availableSymbols.splice(randomIndex, 1);
  }
  
  return code;
};

//     Mastermind
const checkCode = (guess: string[], secret: string[]): LedState[] => {
  const feedback: LedState[] = ['off', 'off', 'off', 'off'];
  const secretCopy = [...secret];
  const guessCopy = [...guess];
  
  //   -  "" ( )
  for (let i = 0; i < 4; i++) {
    if (guessCopy[i] === secretCopy[i]) {
      feedback[i] = 'green';
      secretCopy[i] = ''; //   
      guessCopy[i] = ''; //   
    }
  }
  
  //   -  "" ( ,    )
  for (let i = 0; i < 4; i++) {
    if (guessCopy[i] !== '' && feedback[i] !== 'green') {
      const foundIndex = secretCopy.indexOf(guessCopy[i]);
      if (foundIndex !== -1) {
        feedback[i] = 'yellow';
        secretCopy[foundIndex] = ''; //   
      } else {
        feedback[i] = 'red'; //    
      }
    }
  }
  
  return feedback;
};

const AlienCodeBreaker: React.FC<AlienCodeBreakerProps> = ({ onComplete }) => {
  //   
  const [secretCode] = useState<string[]>(generateSecretCode());
  
  //  
  const [currentGuess, setCurrentGuess] = useState<string[]>(['', '', '', '']);
  const [currentPosition, setCurrentPosition] = useState<number>(0);
  const [guessHistory, setGuessHistory] = useState<CodeGuess[]>([]);
  const [timeLeft, setTimeLeft] = useState<number>(300); // 5 
  const [gameOver, setGameOver] = useState<boolean>(false);
  const [success, setSuccess] = useState<boolean>(false);
  const [attemptsLeft, setAttemptsLeft] = useState<number>(8);
  const [ledStates, setLedStates] = useState<LedState[]>(['off', 'off', 'off', 'off']);
  
  //      
  const handleSymbolClick = useCallback((symbol: string) => {
    if (gameOver || currentPosition >= 4) return;
    
    const newGuess = [...currentGuess];
    newGuess[currentPosition] = symbol;
    setCurrentGuess(newGuess);
    setCurrentPosition(Math.min(currentPosition + 1, 4));
  }, [gameOver, currentPosition, currentGuess]);
  
  //   
  const handleClear = () => {
    if (gameOver || currentPosition <= 0) return;
    
    const newPosition = currentPosition - 1;
    const newGuess = [...currentGuess];
    newGuess[newPosition] = '';
    setCurrentGuess(newGuess);
    setCurrentPosition(newPosition);
  };
  
  //    
  const handleSubmitCode = () => {
    if (gameOver || currentGuess.some(symbol => symbol === '')) return;
    
    const feedback = checkCode(currentGuess, secretCode);
    setLedStates(feedback);
    
    //   
    setGuessHistory(prev => [...prev, {
      symbols: [...currentGuess],
      feedback: [...feedback]
    }]);
    
    //  
    if (feedback.every(state => state === 'green')) {
      setSuccess(true);
      setGameOver(true);
      setTimeout(() => onComplete(true), 2000);
      return;
    }
    
    //   
    const newAttemptsLeft = attemptsLeft - 1;
    setAttemptsLeft(newAttemptsLeft);
    
    //     
    const incorrectCount = feedback.filter(state => state === 'red').length;
    if (incorrectCount > 0) {
      setTimeLeft(prev => Math.max(0, prev - incorrectCount * 15)); // -15    
    }
    
    //  
    if (newAttemptsLeft <= 0) {
      setGameOver(true);
      setTimeout(() => onComplete(false), 1500);
      return;
    }
    
    //   
    setCurrentGuess(['', '', '', '']);
    setCurrentPosition(0);
  };
  
  //  
  useEffect(() => {
    if (gameOver) return;
    
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          setGameOver(true);
          setTimeout(() => onComplete(false), 1500);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
    
    return () => clearInterval(timer);
  }, [gameOver, onComplete]);
  
  //    
  const generateSinusoid = (frequency: number, amplitude: number, phase: number) => {
    const points: string[] = [];
    for (let x = 0; x <= 100; x += 2) {
      const y = 50 + amplitude * Math.sin((x * frequency * Math.PI / 180) + phase);
      points.push(`${x},${y}`);
    }
    return points.join(' ');
  };
  
  return (
    <div className={styles.alienCodeBreakerContainer}>
      <div className={styles.computer}>
        <div className={styles.monitor}>
          <div className={styles.screen}>
            {/*    */}
            <svg className={styles.sinusoids} viewBox="0 0 100 100">
              <polyline
                points={generateSinusoid(2, 15, 0)}
                className={styles.wave1}
              />
              <polyline
                points={generateSinusoid(3, 10, Math.PI / 4)}
                className={styles.wave2}
              />
              <polyline
                points={generateSinusoid(1.5, 20, Math.PI / 2)}
                className={styles.wave3}
              />
            </svg>
            
            {/*  */}
            <div className={styles.header}>
              <div className={styles.title}>ALIEN SIGNAL DECODER</div>
              <div className={styles.status}>
                TIME: {Math.floor(timeLeft / 60)}:{(timeLeft % 60).toString().padStart(2, '0')} | 
                ATTEMPTS: {attemptsLeft}
              </div>
            </div>
            
            {/*     */}
            <div className={styles.codeInput}>
              <div className={styles.inputSymbols}>
                {currentGuess.map((symbol, index) => (
                  <div
                    key={index}
                    className={classNames(styles.symbolSlot, {
                      [styles.active]: index === currentPosition
                    })}
                  >
                    {symbol || '_'}
                  </div>
                ))}
              </div>
              
              <div className={styles.leds}>
                {ledStates.map((state, index) => (
                  <div
                    key={index}
                    className={classNames(styles.led, styles[state])}
                  />
                ))}
              </div>
            </div>
            
            {/*   */}
            <div className={styles.history}>
              {guessHistory.slice(-3).map((guess, index) => (
                <div key={index} className={styles.historyItem}>
                  <div className={styles.historySymbols}>
                    {guess.symbols.map((symbol, i) => (
                      <span key={i} className={styles.historySymbol}>{symbol}</span>
                    ))}
                  </div>
                  <div className={styles.historyLeds}>
                    {guess.feedback.map((state, i) => (
                      <div key={i} className={classNames(styles.historyLed, styles[state])} />
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
        
        {/*  */}
        <div className={styles.keyboard}>
          <div className={styles.symbolKeys}>
            {ALIEN_SYMBOLS.map((symbol, index) => (
              <button
                key={index}
                className={styles.symbolKey}
                onClick={() => handleSymbolClick(symbol)}
                disabled={gameOver}
              >
                {symbol}
              </button>
            ))}
          </div>
          
          <div className={styles.controlKeys}>
            <button
              className={styles.controlKey}
              onClick={handleClear}
              disabled={gameOver || currentPosition <= 0}
            >
              CLEAR
            </button>
            <button
              className={classNames(styles.controlKey, styles.submit)}
              onClick={handleSubmitCode}
              disabled={gameOver || currentGuess.some(s => s === '')}
            >
              TRANSMIT
            </button>
          </div>
        </div>
      </div>
      
      {gameOver && (
        <div className={`${styles.gameOverMessage} ${success ? styles.success : styles.failure}`}>
          {success 
            ? ' !  !' 
            : ' !   ...'}
        </div>
      )}
    </div>
  );
};

export default AlienCodeBreaker; 