import React, { useState, useEffect, useCallback } from 'react';
import styles from './RestartCore.module.scss';
import classNames from 'classnames';

interface RestartCoreProps {
  onComplete: (success: boolean) => void;
}

//    
type PatternType = 'line' | 'spiral' | 'zigzag';

//   
interface Panel {
  id: number;
  isActive: boolean;
  isDisabled: boolean;
  isStart: boolean;
  isFinish: boolean;
  isFalling: boolean; //   
}

//    
const generatePattern = (type: PatternType): number[] => {
  //    0  8 (9 )
  const indices = Array.from({ length: 9 }, (_, i) => i);
  
  switch (type) {
    case 'line':
      //  :  ,  
      return [0, 1, 2, 5, 8, 7, 6, ];
    
    case 'spiral':
      //  :     
      return [0, 1, 4, 7, 8];
    
    case 'zigzag':
      // :   
      return [0, 3, 4, 5, 8];
    
    default:
      //    
      return indices.sort(() => Math.random() - 0.5);
  }
};

const RestartCore: React.FC<RestartCoreProps> = ({ onComplete }) => {
  //    
  const patternTypes: PatternType[] = ['line', 'spiral', 'zigzag'];
  const randomPatternType = patternTypes[Math.floor(Math.random() * patternTypes.length)];
  
  //  
  const [pattern] = useState<number[]>(generatePattern(randomPatternType));
  
  //    
  const createInitialPanels = (): Panel[] => Array.from({ length: 9 }, (_, i) => ({
    id: i,
    isActive: i === pattern[0], //     
    isDisabled: false,
    isStart: i === pattern[0], //   - 
    isFinish: i === pattern[pattern.length - 1], //   - 
    isFalling: false
  }));
  
  const [panels, setPanels] = useState<Panel[]>(createInitialPanels());
  const [activatedPanels, setActivatedPanels] = useState<Set<number>>(new Set([pattern[0]])); //   
  const [timeLeft, setTimeLeft] = useState<number>(60); // 60   
  const [gameOver, setGameOver] = useState<boolean>(false);
  const [success, setSuccess] = useState<boolean>(false);
  const [failedPanels, setFailedPanels] = useState<number[]>([]);
  
  //     
  const resetPanelsExceptFailed = (failedPanelId: number) => {
    setPanels(prev => prev.map(panel => {
      if (panel.id === failedPanelId || failedPanels.includes(panel.id)) {
        //    
        setFailedPanels(prev => [...prev, failedPanelId]);
        return { ...panel, isFalling: true };
      } else {
        //      
        return {
          ...panel,
          isActive: panel.id === pattern[0], //    
          isDisabled: false,
          isFalling: false
        };
      }
    }));
    
    //     
    setActivatedPanels(new Set([pattern[0]]));
    
    //      
    setTimeout(() => {
      setPanels(prev => prev.map(panel => 
        panel.id === failedPanelId 
          ? { ...panel, isDisabled: true, isFalling: false }
          : panel
      ));
    }, 500);
  };
  
  //    
  const handlePanelClick = useCallback((panelId: number) => {
    if (gameOver) return;
    
    //      ,  
    if (panels[panelId].isActive || panels[panelId].isDisabled || panels[panelId].isFalling) return;
    
    // ,     
    if (pattern.includes(panelId)) {
      //   -    
      const newPanels = [...panels];
      newPanels[panelId].isActive = true;
      setPanels(newPanels);
      
      //    
      const newActivatedPanels = new Set(activatedPanels);
      newActivatedPanels.add(panelId);
      setActivatedPanels(newActivatedPanels);
      
      // ,     
      if (newActivatedPanels.size === pattern.length) {
        setSuccess(true);
        setGameOver(true);
        setTimeout(() => onComplete(true), 1500);
      }
    } else {
      //   -    
      resetPanelsExceptFailed(panelId);
      
      //  10 
      setTimeLeft(prev => Math.max(0, prev - 10));

    }
  }, [gameOver, panels, pattern, activatedPanels, onComplete]);
  
  //   
  useEffect(() => {
    if (gameOver) return;
    
    const timer = setInterval(() => {
      setTimeLeft(prev => {
        if (prev <= 1) {
          //  
          setGameOver(true);
          setTimeout(() => onComplete(false), 1500);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
    
    return () => clearInterval(timer);
  }, [gameOver]);
  
  return (
    <div className={styles.restartCoreContainer}>
      <div className={styles.gameHeader}>
        <h3> </h3>
        <div className={styles.timer}>
           : <span className={timeLeft < 15 ? styles.timerWarning : ''}>{timeLeft}s</span>
        </div>
      </div>
      <div className={styles.panelsGrid}>
        {panels.map(panel => (
          <div
            key={panel.id}
            className={classNames(
              styles.panel,
              {
                [styles.active]: panel.isActive,
                [styles.disabled]: panel.isDisabled,
                [styles.start]: panel.isStart,
                [styles.finish]: panel.isFinish,
                [styles.falling]: panel.isFalling
              }
            )}
            onClick={() => handlePanelClick(panel.id)}
          >
            {panel.isStart && <div className={styles.startMarker}></div>}
            {panel.isFinish && <div className={styles.finishMarker}></div>}
          </div>
        ))}
      </div>
      
      
      <div className={styles.progress}>
        : {activatedPanels.size} / {pattern.length}
      </div>
      
      {gameOver && (
        <div className={`${styles.gameOverMessage} ${success ? styles.success : styles.failure}`}>
          {success ? ' !' : ' ,    !'}
        </div>
      )}
    </div>
  );
};

export default RestartCore; 