import { createSlice, PayloadAction, createAsyncThunk } from '@reduxjs/toolkit';
import { RootState } from './store';
import { GameState } from '../types';

export interface CoinEffect {
  id: number;
  position: { x: number; y: number };
  value: number | string;
  prefix?: string; //    (, '+')
  suffix?: string; //    (, '', 'x', '%')
}

interface VisualEffectsState {
  coins: CoinEffect[];
  nextCoinId: number;
}

const initialState: VisualEffectsState = {
  coins: [],
  nextCoinId: 0,
};

export type CoinEffectValue = number | string | (number | string)[] | ((state: GameState) => string | number | (string | number)[]);

export interface CoinEffectPayload {
  position: { x: number; y: number };
  value: CoinEffectValue;
  prefix?: string;
  suffix?: string;
}

// Payload   ,    
interface InternalCoinEffectPayload {
  position: { x: number; y: number };
  value: string | number | (string | number)[];
  prefix?: string;
  suffix?: string;
}

// Thunk    
export const showCoinEffectThunk = createAsyncThunk(
  'visualEffects/showCoinEffectThunk',
  async (payload: CoinEffectPayload, { dispatch, getState }) => {
    //   
    const gameState = (getState() as RootState).game;
    
    //  ,    
    let processedValue: string | number | (string | number)[];
    
    if (typeof payload.value === 'function') {
      processedValue = payload.value(gameState);
    } else {
      processedValue = payload.value as string | number | (string | number)[];
    }
    
    //      
    dispatch(showCoinEffect({
      position: payload.position,
      value: processedValue,
      prefix: payload.prefix,
      suffix: payload.suffix
    }));
  }
);

export const visualEffectsSlice = createSlice({
  name: 'visualEffects',
  initialState,
  reducers: {
    showCoinEffect: (state, action: PayloadAction<InternalCoinEffectPayload>) => {
      const { position, value, prefix = '', suffix = '' } = action.payload;
      
      if (Array.isArray(value)) {
        //    ,      
        value.forEach((val, index) => {
          state.coins.push({
            id: state.nextCoinId++,
            position: { 
              x: position.x,
              y: position.y - (index * 20) //       
            },
            value: val,
            prefix,
            suffix
          });
        });
      } else {
        //    ,   
        state.coins.push({
          id: state.nextCoinId++,
          position,
          value,
          prefix,
          suffix
        });
      }
    },
    removeCoinEffect: (state, action: PayloadAction<number>) => {
      state.coins = state.coins.filter(coin => coin.id !== action.payload);
    },
  },
});

export const { showCoinEffect, removeCoinEffect } = visualEffectsSlice.actions;

export const selectCoinEffects = (state: RootState) => state.visualEffects.coins;

export default visualEffectsSlice.reducer; 