import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { activateLaterTalentEffect, selectGame, selectSpheres, selectTalents } from '../../app/gameSlice';
import { showCoinEffectThunk } from '../../app/visualEffectsSlice';
import styles from './index.module.scss';
import { TALENTS } from '../../Talents';
import classnames from 'classnames';
import { Talent as TalentType } from '../../types';
import TalentTooltip from './TalentTooltip';

// New action type for activating talents
interface ActivateTalentAction {
  type: 'game/activateTalent';
  payload: string;
}

// Interface for tooltip data
interface TooltipData {
  isVisible: boolean;
  talentId: string;
  position: { x: number; y: number };
  boundaryAdjustment: 'left' | 'right' | 'center'; //    
}

//     
const getCategoryClass = (category: string): string => {
  if (category === 'economy') return styles.economy;
  if (category === 'science') return styles.science;
  if (category === 'faith') return styles.faith;
  return '';
};

// Talent component to render individual talents
const Talent: React.FC<{
  id: string;
  name: string;
  description: string;
  category: string;
  icon: string;
  position: [number, number];
  isVisible: boolean;
  isActive: boolean;
  isShaded: boolean;
  isLocked: boolean;
  onClick: () => void;
  dependencyLinks?: Array<[number, number]>;
  onShowTooltip: (talentId: string, e: React.MouseEvent) => void;
  onHideTooltip: () => void;
  space: boolean;
}> = ({ 
  id, 
  name,
  description,
  category,
  icon, 
  position, 
  isVisible, 
  isActive, 
  isLocked,
  isShaded,
  onClick,
  dependencyLinks,
  onShowTooltip,
  onHideTooltip,
  space
}) => {
  if (!isVisible) return null;

  const talentStyle = {
    gridColumn: position[0],
    gridRow: space ? 251 - position[1] : 51 - position[1],
  };
  
  const talentClasses = classnames(
    styles.talent,
    { [styles.active]: isActive },
    { [styles.locked]: isLocked },
    { [styles.unlocked]: !isLocked },
    { [styles.shaded]: isShaded }
  );

  return (
    <>
      {/* Draw dependency lines to connected talents */}
      {dependencyLinks?.map((targetPos, index) => (
        <div 
          key={`${id}-dep-${index}`}
          className={styles.talentDependency}
          style={{
            left: `${position[0] * 100 + 25}px`,
            bottom: `${position[1] * 100 + 25}px`,
            width: `${Math.sqrt(
              Math.pow((targetPos[0] - position[0]) * 100, 2) + 
              Math.pow((targetPos[1] - position[1]) * 100, 2)
            )}px`,
            transform: `rotate(${Math.atan2(
              (targetPos[1] - position[1]) * 100,
              (targetPos[0] - position[0]) * 100
            )}rad)`,
            transformOrigin: 'left center'
          }}
        />
      ))}
      
      {/* Talent node */}
      <div 
        id={id}
        className={talentClasses}
        style={talentStyle}
        onClick={!isActive && !isLocked ? onClick : undefined}
        onMouseEnter={(e) => onShowTooltip(id, e)}
        onMouseLeave={onHideTooltip}
        tabIndex={!isActive && !isLocked ? 0 : -1} //    
        onKeyDown={(e) => {
          if (e.key === 'Enter' && !isActive && !isLocked) {
            onClick();
          }
        }}
        aria-label={` ${name},  ${category}${isActive ? ', ' : isLocked ? ', ' : ',   '}`}
      >
        <div className={styles.iconContainer}>
          <div className={styles.icon}>{icon}</div>
          <div className={styles.talentName}>{name}</div>
        </div>
      </div>
    </>
  );
};

// ResourceBar component to show progress for each resource type
const ResourceBar: React.FC<{ 
  category: string;
  currentResources: number;
}> = ({ category, currentResources }) => {
  const game = useAppSelector(selectGame);
  const fillValue = currentResources / 20;
  
  // ,      
  const isShieldActive = game.stage === 2 && 
                         game.ship.activeShields[category];
  
  //        
  //   ,    
  const usageIntensity = isShieldActive ? 50 : 10;
  
  //    
  const flameEffect = useMemo(() => {
    if (!isShieldActive) return null;
    
    //       
    const sparkCount = Math.min(20 + Math.floor(usageIntensity / 2), 50);
    
    //       
    const randomRange = (min: number, max: number) => Math.random() * (max - min) + min;
    
    //      
    const flameColors = {
      economy: ['#FFD700', '#FFA500', '#FF8C00', '#FF4500'],
      science: ['#FFD700', '#FFA500', '#FF8C00', '#FF4500'],
      faith: ['#FFD700', '#FFA500', '#FF8C00', '#FF4500']
    };
    
    const colors = flameColors[category as keyof typeof flameColors] || flameColors.economy;
    
    //     
    return Array.from({ length: sparkCount }, (_, i) => {
      //      
      const size = randomRange(2, 5); //   px
      const baseDelay = i * (0.05 + Math.random() * 0.1); //      
      const duration = randomRange(0.5, 2.0) * (1 + usageIntensity / 100); //     
      const xOffset = randomRange(-40, 40) * (1 + usageIntensity / 200); //   X   
      const yOffset = randomRange(30, 120) * (1 + usageIntensity / 150); //   Y ()   
      const opacity = randomRange(0.3, 1.0);
      const color = colors[Math.floor(Math.random() * colors.length)]; //    
      const rotationSpeed = randomRange(-360, 360); //  
      const scale = randomRange(0.3, 1.5); //  
      
      //    
      const sparkStyle: React.CSSProperties = {
        position: 'absolute',
        width: `${size}px`,
        height: `${size}px`,
        backgroundColor: color,
        borderRadius: '50%',
        top: '0',
        left: `${randomRange(20, 80)}%`,
        boxShadow: `0 0 ${size * 2}px ${color}`,
        zIndex: 5,
        opacity: opacity,
        animationDuration: `${duration}s`,
        animationDelay: `${baseDelay}s`,
        transform: 'translateZ(0)', //  
        //   CSS     keyframes
        '--spark-x': `${xOffset}px`,
        '--spark-y': `${yOffset}px`,
        '--rotation': `${rotationSpeed}deg`,
        '--scale': scale.toString(),
        '--opacity': opacity.toString(),
      } as React.CSSProperties;
      
      return <div key={`spark-${i}`} className={styles.spark} style={sparkStyle} />;
    });
  }, [isShieldActive, usageIntensity, category]);
  
  //    
  const flameBaseStyle = useMemo(() => {
    if (!isShieldActive) return {};
    
    //     
    const baseColors = {
      economy: 'radial-gradient(ellipse at center, rgba(255, 215, 0, 0.8) 0%, rgba(255, 165, 0, 0.5) 40%, rgba(255, 69, 0, 0.3) 70%, transparent 100%)',
      science: 'radial-gradient(ellipse at center, rgba(255, 215, 0, 0.8) 0%, rgba(255, 165, 0, 0.5) 40%, rgba(255, 69, 0, 0.3) 70%, transparent 100%)',
      faith: 'radial-gradient(ellipse at center, rgba(255, 215, 0, 0.8) 0%, rgba(255, 165, 0, 0.5) 40%, rgba(255, 69, 0, 0.3) 70%, transparent 100%)'
    };
    
    const flameHeight = Math.min(15 + usageIntensity / 1.5, 70);
    
    return {
      position: 'absolute' as const,
      top: '-5px',
      left: '0',
      width: '100%',
      height: `${flameHeight}px`,
      background: baseColors[category as keyof typeof baseColors] || baseColors.economy,
      borderRadius: '50% 50% 0 0',
      filter: 'blur(5px)',
      zIndex: 4,
      animation: `flame-pulse ${1 + Math.random() * 0.5}s infinite alternate`,
      transform: 'translateZ(0)', //  
    } as React.CSSProperties;
  }, [isShieldActive, usageIntensity, category]);
  
  //       
  const additionalFlameElements = useMemo(() => {
    if (!isShieldActive) return null;
    
    //      
    const innerColors = {
      economy: 'radial-gradient(ellipse at center, rgba(255, 255, 200, 0.9) 0%, rgba(255, 215, 0, 0.7) 30%, rgba(255, 165, 0, 0.4) 60%, transparent 100%)',
      science: 'radial-gradient(ellipse at center, rgba(255, 255, 200, 0.9) 0%, rgba(255, 215, 0, 0.7) 30%, rgba(255, 165, 0, 0.4) 60%, transparent 100%)',
      faith: 'radial-gradient(ellipse at center, rgba(255, 255, 200, 0.9) 0%, rgba(255, 215, 0, 0.7) 30%, rgba(255, 165, 0, 0.4) 60%, transparent 100%)'
    };
    
    //    ( )
    const innerFlame = {
      position: 'absolute' as const,
      top: '-2px',
      left: '25%',
      width: '50%',
      height: `${Math.min(10 + usageIntensity / 2, 40)}px`,
      background: innerColors[category as keyof typeof innerColors] || innerColors.economy,
      borderRadius: '50% 50% 50% 50%',
      filter: 'blur(3px)',
      zIndex: 5,
      animation: 'flame-pulse 0.8s infinite alternate',
      transform: 'translateZ(0)', //  
    } as React.CSSProperties;
    
    return (
      <>
        <div style={innerFlame} />
      </>
    );
  }, [isShieldActive, usageIntensity, category]);
  
  return (
    <div className={`${styles.resourceBar} ${styles[category]}`}>
      <div 
        className={`${styles.resourceFill} ${isShieldActive ? styles.activeShield : ''}`}
        style={{ height: `${fillValue}px` }}
      >
        {/*   */}
        {isShieldActive && <div style={flameBaseStyle} />}
        
        {/*    */}
        {additionalFlameElements}
        
        {/*   */}
        {flameEffect}
        
        {/*       */}
        {isShieldActive && game.stage === 2 && (
          <div className={styles.sparkFixed}>
            <div className={styles.spark}></div>
            <div className={styles.spark}></div>
            <div className={styles.spark}></div>
            <div className={styles.spark}></div>
          </div>
        )}
      </div>
      <div className={styles.resourceLabel}>
        {Math.round(currentResources)}
      </div>
    </div>
  );
};

// Main TalentTree component
const TalentTree: React.FC = () => {
  const dispatch = useAppDispatch();
  const spheres = useAppSelector(selectSpheres);
  const containerRef = useRef<HTMLDivElement>(null);
  const tooltipRef = useRef<HTMLDivElement>(null);
  const game = useAppSelector(selectGame);
  
  //  ,   gameLoop
  const talents = useAppSelector(selectTalents);

  //   
  const [tooltip, setTooltip] = useState<TooltipData>({ 
    isVisible: false, 
    talentId: '',
    position: { x: 0, y: 0 },
    boundaryAdjustment: 'center'
  });

  useEffect(() => {
    if (containerRef.current) {
      //     
      containerRef.current.scrollTop = containerRef.current.scrollHeight;
    }
  }, []);
  
  // Handle talent activation
  const activateTalent = (talentId: string) => {
    //    TALENTS   meta 
    const talent = TALENTS.find(t => t.id === talentId);
    
    if (talent) {
      //      
      const talentElement = document.getElementById(talentId);
      
      if (talentElement) {
        const rect = talentElement.getBoundingClientRect();
        const position = {
          x: rect.left + rect.width / 2,
          y: rect.top + rect.height / 2
        };
        
        //   popup 
        if (talent.meta?.popup) {
          dispatch(showCoinEffectThunk({
            position: {
              x: position.x,
              y: position.y 
            },
            value: talent.meta?.popup,
            prefix: '',
            suffix: ''
          }));
        }
        
        //   popup 
        if (talent.meta?.laterPopup) {
          Object.entries(talent.meta.laterPopup).forEach(([delay, values]) => {
            const delayMs = parseInt(delay);
            
            setTimeout(() => {
              dispatch(showCoinEffectThunk({
                position: {
                  x: position.x,
                  y: position.y
                },
                value: values,
                prefix: '+',
                suffix: ''
              }));
            }, delayMs);
          });
        }
        if (talent?.laterEffects) {
          Object.keys(talent.laterEffects).forEach((delay) => {
            const delayMs = parseInt(delay);
            setTimeout(() => {
              dispatch(activateLaterTalentEffect(talentId));
            }, delayMs);
          });
        }
      }
    }
    
    // Dispatch action to Redux
    dispatch({
      type: 'game/activateTalent',
      payload: talentId
    } as ActivateTalentAction);
  };

  //     
  const adjustTooltipPosition = (
    x: number, 
    y: number, 
    talentId: string
  ): { position: { x: number; y: number }, boundaryAdjustment: 'left' | 'right' | 'center' } => {
    //   
    const tooltipWidth = 280;
    const windowWidth = window.innerWidth;
    const windowHeight = window.innerHeight;
    const padding = 20; //    
    
    //    X
    let adjustedX = x;
    let adjustedY = y;
    let boundaryAdjustment: 'left' | 'right' | 'center' = 'center';
    
    //      
    if (x - tooltipWidth / 2 < padding) {
      adjustedX = padding + tooltipWidth / 2;
      boundaryAdjustment = 'left';
    }
    //      
    else if (x + tooltipWidth / 2 > windowWidth - padding) {
      adjustedX = windowWidth - padding - tooltipWidth / 2;
      boundaryAdjustment = 'right';
    }
    
    //    (   )
    const estimatedTooltipHeight = 200;
    
    //       
    if (y + estimatedTooltipHeight > windowHeight - padding) {
      //    
      const talent = document.getElementById(talentId);
      if (talent) {
        const rect = talent.getBoundingClientRect();
        adjustedY = rect.top - 10;
      } else {
        adjustedY = windowHeight - padding - estimatedTooltipHeight;
      }
    }
    
    return { 
      position: { x: adjustedX, y: adjustedY },
      boundaryAdjustment
    };
  };

  //   
  const handleShowTooltip = (talentId: string, e: React.MouseEvent) => {
    const rect = e.currentTarget.getBoundingClientRect();
    const originalX = rect.left + rect.width / 2;
    const originalY = rect.bottom + 5;
    
    //  
    const adjustedPosition = adjustTooltipPosition(originalX, originalY, talentId);
    
    setTooltip({
      isVisible: true,
      talentId,
      ...adjustedPosition
    });
  };

  const handleHideTooltip = () => {
    setTooltip(prev => ({ ...prev, isVisible: false }));
  };

  //     
  const tooltipTalent = useMemo(() => {
    if (!tooltip.isVisible) return null;
    const talent = TALENTS.find(t => t.id === tooltip.talentId);
    return talent as TalentType | null;
  }, [tooltip.isVisible, tooltip.talentId]);

  const viewTalents = useMemo(()=>{
    return TALENTS.map(talent => {
      const foundTalent = talents.find(t => t.id === talent.id);
      return {
        ...talent,
        ...foundTalent
      };
    });
  },[talents]);
  
  return (
    <div 
      ref={containerRef} 
      className={classnames(styles.talentTreeContainer, {
        [styles.space]: game.stage === 2
      })}
      role="region" 
      aria-label=" "
    >
      {/* Resource bars for each sphere */}
      <div className={styles.resourceBars}>
        {Object.entries(spheres).map(([key, sphere]) => (
          <ResourceBar
            key={key}
            category={key}
            currentResources={sphere.resources}
          />
        ))}
      </div>
      
      {/* Talent tree */}
      <div className={styles.talentTree}>
        {viewTalents.map(talent => (
          <Talent
            key={talent.id}
            id={talent.id}
            name={talent.name || ''}
            description={talent.description || ''}
            category={talent.category}
            icon={talent.icon}
            position={talent.position}
            isVisible={talent.isVisible || false}
            isActive={talent.state === 'active'}
            isShaded={talent.isShaded || false}
            isLocked={!talent.isAvailable}
            dependencyLinks={talent.meta?.dependencyLinks}
            onClick={() => activateTalent(talent.id)}
            onShowTooltip={handleShowTooltip}
            onHideTooltip={handleHideTooltip}
            space={game.stage === 2}
          />
        ))}
      </div>

      {/* Tooltip using separated component */}
      {tooltip.isVisible && tooltipTalent && (
        <TalentTooltip 
          talent={tooltipTalent}
          position={tooltip.position}
          boundaryAdjustment={tooltip.boundaryAdjustment}
          tooltipRef={tooltipRef}
        />
      )}
    </div>
  );
};

export default TalentTree; 